import Vue from 'vue';
import { AntIcon } from '../AntIcon';
export default Vue.extend({
    components: {
        AntIcon
    },
    props: {
        title: String,
        description: String,
        type: String
    },
    methods: {
        renderIcon() {
            const { type } = this;
            if (type === 'error') {
                return this.$createElement('ant-icon', {
                    class: 'error',
                    props: {
                        type: 'close-circle'
                    }
                });
            }
            if (type === 'success') {
                return this.$createElement('ant-icon', {
                    class: 'success',
                    props: {
                        type: 'check-circle'
                    }
                });
            }
            return this.$createElement();
        }
    },
    render(h) {
        const { title, description } = this;
        return h('div', {
            class: 'result',
            props: this.$attrs,
            on: this.$listeners
        }, [
            h('div', { class: 'icon' }, [this.renderIcon()]),
            h('div', { class: 'title' }, title || this.$slots.title),
            (description || this.$slots.description) &&
                h('div', { class: 'description' }, description || this.$slots.description),
            this.$slots.extra && h('div', { class: 'extra' }, this.$slots.extra),
            this.$slots.actions &&
                h('div', { class: 'actions' }, this.$slots.actions)
        ]);
    }
});



